@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1150px;
  padding-right: 30px;
  padding-left: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "Opensans", "Arial", sans-serif;
  background-color: #ffffff;
  padding: 16px;
  color: #444444;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  border: none;
  box-shadow: inset 0 0 0 2px #444444;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.page {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: #ffffff;
}

.block-decorated {
  position: relative;
}

.block-decorated--gray-waves::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 6px;
  background-image: url("../img/svg/waves-gray-mobile.svg");
  background-repeat: repeat-x;
  bottom: 0;
  left: 0;
}
@media screen and (min-width: 768px) {
  .block-decorated--gray-waves::before {
    height: 17px;
    background-image: url("../img/svg/waves-gray.svg");
  }
}
@media screen and (min-width: 1150px) {
  .block-decorated--gray-waves::before {
    width: calc(100% - 100px);
    left: unset;
    height: 23px;
    background-image: url("../img/svg/waves-gray-desktop.svg");
  }
}

@media screen and (min-width: 768px) {
  .block-decorated--white-waves::after {
    position: absolute;
    z-index: 1;
    content: "";
    width: 100%;
    height: 24px;
    bottom: -1px;
    background-image: url("../img/svg/waves-white.svg");
    background-repeat: repeat-x;
    background-position: center;
  }
}
@media screen and (min-width: 1150px) {
  .block-decorated--white-waves::after {
    background-image: url("../img/svg/waves-white-desktop.svg");
    height: 34px;
  }
}

.page-body {
  color: #000000;
  background-color: #ffffff;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0 auto;
}

.page-body__header-wrapper {
  position: relative;
  width: 100%;
  z-index: 1;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media screen and (min-width: 768px) {
  .page-body__header-wrapper {
    overflow: hidden;
    box-shadow: none;
  }
}

.header-main {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .header-main::before {
    position: absolute;
    content: "";
    width: 100%;
    left: 0;
    right: 0;
    height: 100%;
    min-height: 62px;
    background-color: #fafafa;
    border-top: 1px solid #ececec;
    z-index: -1;
  }
}

.header-main__logo-wrapper {
  display: flex;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 22px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-wrapper {
    position: absolute;
    left: 50px;
    z-index: 5;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-wrapper {
    left: 50%;
    transform: translateX(-50%);
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.header-main__logo {
  position: relative;
  text-decoration: none;
  width: 86px;
  height: 35px;
  background-image: url("../img/svg/logo-mobile.svg");
  background-repeat: no-repeat;
  z-index: 5;
}
@media screen and (min-width: 768px) {
  .header-main__logo {
    width: 113px;
    height: 46px;
    top: 0;
    background-image: url("../img/svg/logo-tablet.svg");
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo {
    width: 149px;
    height: 72px;
    background-image: url("../img/svg/logo-desktop.svg");
  }
}
.header-main__logo:hover {
  opacity: 0.8;
}
.header-main__logo:active {
  opacity: 0.6;
}

.header-main__logo-image--not-mobile {
  padding-left: 6px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-image--not-mobile {
    color: #444444;
    padding-left: 5px;
    width: 67px;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-image {
    width: 38px;
    height: 22px;
  }
  .header-main__logo-image--not-mobile {
    width: 78px;
    padding-left: 8px;
  }
}

.main-nav {
  display: flex;
  background-color: transparent;
}
@media screen and (min-width: 768px) {
  .main-nav {
    width: 100%;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: transparent;
  }
  .main-nav__list--site-nav {
    grid-column: 2/3;
    grid-row: 1/2;
    grid-template-columns: 84px 1fr;
    padding-right: 50px;
    position: relative;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list {
    background-color: transparent;
    max-width: 100%;
  }
  .main-nav__list--site-nav {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__list--main-nav {
    padding-left: 50px;
    background-color: transparent;
    column-gap: 15px;
  }
  .main-nav__list--main-nav .main-nav__link {
    padding: 21px 0 19px 0;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav {
    box-shadow: none;
    padding-left: 15px;
    column-gap: 0;
  }
}

@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
    min-height: 88px;
  }
  .main-nav__list--main-nav .main-nav__item:nth-child(odd) {
    box-shadow: 1px 0 0 0 rgba(0, 0, 0, 0.1);
  }
}

@media screen and (min-width: 768px) {
  .main-nav__list--site-nav::before {
    position: absolute;
    content: "";
    width: 200vw;
    right: -100vw;
    height: 100%;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
    z-index: -1;
  }
}
.main-nav__list--site-nav .main-nav__link:hover .main-nav__icon-img {
  color: #63d1bb;
  opacity: 1;
}
.main-nav__list--site-nav .main-nav__link:hover .main-nav__link-text {
  opacity: 0.6;
}
.main-nav__list--site-nav .main-nav__link:active {
  opacity: 0.6;
}
.main-nav__list--site-nav .main-nav__item {
  position: relative;
}
.main-nav__list--site-nav .main-nav__icon-img {
  position: absolute;
  left: 31px;
  top: 31px;
}
@media screen and (min-width: 768px) {
  .main-nav__list--site-nav .main-nav__icon-img {
    top: 35px;
  }
}

.main-nav__list--customer-nav {
  display: none;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--customer-nav {
    padding-left: 15px;
    display: grid;
  }
}

@media screen and (min-width: 1150px) {
  .main-nav__list--customer-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
  }
  .main-nav__list--customer-nav .main-nav__item .main-nav__link {
    padding: 19px 0 19px 0;
  }
}

.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media screen and (min-width: 768px) {
  .main-nav__item {
