    padding-left: 82px;
    padding-right: 0;
  }
}

.main-nav__link {
  display: flex;
  width: 100%;
  padding: 28px 27px 27px 76px;
  color: #000000;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 17px;
