  }
}

.main-nav__delivery {
  color: #444444;
  display: none;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
}
@media screen and (min-width: 768px) {
  .main-nav__delivery {
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__delivery {
    grid-column: 3/4;
  }
}

.main-nav__delivery-text {
  margin: 0;
}
@media screen and (min-width: 768px) {
  .main-nav__delivery-text {
    padding-top: 21px;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 29px;
  right: 30px;
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/close-menu.svg");
  background-color: inherit;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  display: flex;
  border: none;
  cursor: pointer;
}
.no-js .main-nav__toggle {
  display: none;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle.main-nav__toggle--close {
  right: 31px;
  top: 32px;
  width: 20px;
  height: 14px;
  background-image: url("../img/svg/open-menu.svg");
}

.pop-up-menu {
  display: none;
}
@media screen and (min-width: 768px) {
  .pop-up-menu {
    display: grid;
    grid-template-columns: 48% 52%;
    grid-auto-flow: dense;
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu {
    grid-template-columns: 450px 1fr 400px;
  }
}
.no-js .pop-up-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .no-js .pop-up-menu {
    padding-top: 0;
    display: grid;
  }
}
.pop-up-menu::before {
  content: "";
  width: 100%;
}
@media screen and (min-width: 768px) {
  .pop-up-menu::before {
    grid-column: 1/2;
    grid-row: span 1;
    padding-left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu::before {
    grid-column: 2/3;
    grid-row: 1/2;
    padding-left: 0;
  }
}

.pop-up-menu--is-open {
  position: absolute;
  z-index: 10;
  display: flex;
  flex-direction: column;
  width: 100%;
  left: 0;
}
@media screen and (min-width: 768px) {
  .pop-up-menu--is-open {
    z-index: 0;
    position: static;
    display: grid;
  }
}

.pop-up-menu__button-close {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  padding: 0;
  display: flex;
  border: none;
  background-color: transparent;
  background-image: url("../img/svg/icon-cross.svg");
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .pop-up-menu__button-close {
    display: none;
  }
}
.pop-up-menu__button-close:hover {
  opacity: 0.6;
}
.pop-up-menu__button-close:active {
  opacity: 0.3;
}

